<template>
  <div>
    <el-card id='right_id' style="float:right;width:-webkit-calc(100% - 350px);">
      <div v-if="right_api">
        <el-button @click="send_api" size="mini" type="primary"></el-button>
        <el-button @click="save_api" size="mini" type="primary"></el-button>
        <el-button @click="up_api" size="mini" type="text">up</el-button>
        <el-button @click="down_api" size="mini" type="text">down</el-button>
        <span style="float: right">ID:{{ setting_api.id }}</span><br><br>
        <el-form ref="form" label-width="100px" label-position="left">
          <el-form-item label="/">
            <el-input v-model="setting_api.label" style="width: 55%"></el-input>
            <el-input v-model="setting_api.des" style="width: 45%"></el-input>
          </el-form-item>
          <el-form-item label="Host">
            <el-input v-model="setting_api.host"></el-input>
          </el-form-item>
          <el-form-item label="/">
            <el-select v-model="setting_api.method" style="width: 100px" placeholder="">
              <el-option label="POST" value="POST"></el-option>
              <el-option label="GET" value="GET"></el-option>
            </el-select>
            <el-input style="width: -webkit-calc(100% - 112px)" v-model="setting_api.path"></el-input>
          </el-form-item>
          <el-tabs type="border-card" v-model="choose_tab_pane">
            <el-tab-pane label="Params">
              <el-table
                  :data="setting_api.params"
                  stripe
                  size="mini"
                  style="width: 100%;overflow-y: auto;min-height: 100%">
                  <el-table-column label="" width="180">
                    <template slot-scope="scope">
                      <el-input v-model="scope.row.key"></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column label="" width="180">
                    <template slot-scope="scope">
                      <el-input v-model="scope.row.value"></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column label="" width="180">
                    <template slot-scope="scope">
                      <el-input v-model="scope.row.des"></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column>
                    <template slot="header">
                      <el-button size="mini" type="primary" @click="add_params"></el-button>
                    </template>
                     <template slot-scope="scope">
                      <el-button size="mini" type="danger" @click="del_params(scope.$index)"></el-button>
                    </template>
                  </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label="Headers">
              <el-table
                  :data="setting_api.headers"
                  stripe
                  size="mini"
                  style="width: 100%;overflow-y: auto;min-height: 100%">
                  <el-table-column label="" width="180">
                    <template slot-scope="scope">
                      <el-input v-model="scope.row.key"></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column label="" width="180">
                    <template slot-scope="scope">
                      <el-input v-model="scope.row.value"></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column label="" width="180">
                    <template slot-scope="scope">
                      <el-input v-model="scope.row.des"></el-input>
                    </template>
                  </el-table-column>
                  <el-table-column>
                    <template slot="header">
                      <el-button size="mini" type="primary" @click="add_headers"></el-button>
                    </template>
                     <template slot-scope="scope">
                      <el-button size="mini" type="danger" @click="del_headers(scope.$index)"></el-button>
                    </template>
                  </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label="Body">
              <el-tabs v-model="setting_api.payload_method" @tab-click="handleClick">
                <el-tab-pane label="none" name="none"><h2 style="text-align: center;color: darkgrey"></h2>
                </el-tab-pane>
                <el-tab-pane label="form-data" name="form-data">
                  <el-table
                      :data="setting_api.payload_fd"
                      stripe
                      size="mini"
                      style="width: 100%;overflow-y: auto;min-height: 100%">
                      <el-table-column label="" width="180">
                    <template slot-scope="scope">
                      <el-input v-model="scope.row.key"></el-input>
                    </template>
                  </el-table-column>
                      <el-table-column label="" width="180">
                    <template slot-scope="scope">
                      <el-input v-model="scope.row.value"></el-input>
                    </template>
                  </el-table-column>
                      <el-table-column label="" width="180">
                    <template slot-scope="scope">
                      <el-input v-model="scope.row.des"></el-input>
                    </template>
                  </el-table-column>
                      <el-table-column>
                          <template slot="header">
                            <el-button size="mini" type="primary" @click="add_payload_fd"></el-button>
                          </template>
                           <template slot-scope="scope">
                            <el-button size="mini" type="danger" @click="del_payload_fd(scope.$index)"></el-button>
                          </template>
                      </el-table-column>
                  </el-table>
                </el-tab-pane>
                <el-tab-pane label="x-www-form-urlencode" name="x-www-form-urlencode">
                  <el-table
                      :data="setting_api.payload_xwfu"
                      stripe
                      size="mini"
                      style="width: 100%;overflow-y: auto;min-height: 100%">
                      <el-table-column label="" width="180">
                  <template slot-scope="scope">
                    <el-input v-model="scope.row.key"></el-input>
                  </template>
                </el-table-column>
                      <el-table-column label="" width="180">
                  <template slot-scope="scope">
                    <el-input v-model="scope.row.value"></el-input>
                  </template>
                </el-table-column>
                      <el-table-column label="" width="180">
                  <template slot-scope="scope">
                    <el-input v-model="scope.row.des"></el-input>
                  </template>
                </el-table-column>
                      <el-table-column>
                  <template slot="header">
                    <el-button size="mini" type="primary" @click="add_payload_xwfu"></el-button>
                  </template>
                   <template slot-scope="scope">
                    <el-button size="mini" type="danger" @click="del_payload_xwfu(scope.$index)"></el-button>
                  </template>
                </el-table-column>
                  </el-table>
                </el-tab-pane>
                <el-tab-pane label="raw" name="raw">
                  <el-select v-model="setting_api.payload_raw_method" style="z-index:999;position:fixed;right:42px;width: 130px"
                             placeholder="Text">
                    <el-option label="Text" value="Text"></el-option>
                    <el-option label="JavaScript" value="JavaScript"></el-option>
                    <el-option label="JSON" value="JSON"></el-option>
                    <el-option label="XML" value="XML"></el-option>
                    <el-option label="HTML" value="HTML"></el-option>
                  </el-select>
                  <el-input v-model="setting_api.payload_raw" type="textarea" :rows="5"></el-input>
                </el-tab-pane>
                <el-tab-pane label="binary" name="binary">
                  <el-upload
                      class="upload-demo"
                      drag
                      action="https://jsonplaceholder.typicode.com/posts/"
                      multiple>
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text"><em></em></div>
                    <div class="el-upload__tip" slot="tip">jpg/png</div>
                  </el-upload>
                </el-tab-pane>
                <el-tab-pane label="GraphQL" name="GraphQL">
                  <el-input v-model="setting_api.payload_GQL_q" type="textarea" :rows="6" style="width: 65%" placeholder="QUERY"></el-input>
                  <el-input v-model="setting_api.payload_GQL_g" type="textarea" :rows="6" style="width: 35%" placeholder="GRAPHQL VARIABLES"></el-input>
                </el-tab-pane>
              </el-tabs>
            </el-tab-pane>
            <el-tab-pane name="Response" label="Response">
              <el-input v-model="response_data.R" type="textarea" style="height: 100%" :rows="6"></el-input>
            </el-tab-pane>
            <el-tab-pane label="ResponseData">
              <el-input v-model="response_data.RD" type="textarea" style="height: 100%" :rows="6"></el-input>
            </el-tab-pane>
            <el-tab-pane label="ConfigureResult">
              <el-input v-model="response_data.CR" type="textarea" style="height: 100%" :rows="6"></el-input>
            </el-tab-pane>
          </el-tabs>
        </el-form>
      </div>
      <div v-else-if="right_configure">
        <el-button size="mini" type="primary" @click="save_configure">Save</el-button>
        <el-button size="mini" type="text" @click="up_configure">up</el-button>
        <el-button size="mini" type="text" @click="down_configure">down</el-button>
        &#12288 <span style="float:right;">ID: {{ setting_configure.id }}</span>
        <br>
        &#12288 <span></span>
        <el-select v-model="setting_configure.do_time" placeholder="" style="width: 90px">
          <el-option label="before" value="before"></el-option>
          <el-option label="after" value="after"></el-option>
        </el-select>
        &#12288 <span></span>
        <el-input style="width:-webkit-calc(100% - 299px)" v-model="setting_configure.label"></el-input>
        <br><br>
        <el-tabs v-model="setting_configure.method" @tab-click="choose_configure_method" tab-position="left" style="height: 100%">
<!--          <el-tab-pane label="">-->
<!--            <span class="smallfont"></span>-->
<!--          </el-tab-pane>-->
          <el-tab-pane name="" label="">
            <el-select v-model="setting_configure.select" style="width: 120px">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="SQL" value="SQL"></el-option>
            </el-select>
            <el-input v-model="setting_configure.value" placeholder="sql" style="width: -webkit-calc(100% - 175px )"></el-input>
            <br>
            <p></p>
            <p>1..find() * </p>
            <p>2.**</p>
            <p>3.* ** json</p>
            <p>4.sqlsql* * *</p>
            <p>{"errorCode":200,"data":[{"name":"lisi"},{"name":"zhangsan"},{"name":"wang"}]}
            <p>&#12288;zhangsan</p>
            <p>&#12288;name":"(.*?)"}==zhangsan</p>
            <p>&#12288;["data"][1]["name"]=="zhangsan"</p>
            <p>&#12288;sqlselect name from  where id=5; == "zhangsan"</p>
            <p>&#12288;sqlselect name from  where name='zhangsan'; (0)</p>

          </el-tab-pane>
          <el-tab-pane name="" label="">
            <el-select v-model="setting_configure.select" style="width: 120px">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="sql" value="sql"></el-option>
            </el-select>
            <el-input v-model="setting_configure.value" placeholder="sql" style="width: -webkit-calc(100% - 175px )"></el-input>
            <br>
            <p></p>
            <p>1.=json</p>
            <p>2.=(.*?)</p>
            <p>3.sql=sql select  0</p>

          </el-tab-pane>
          <el-tab-pane name="SQL" label="SQL">
            <el-input type="textarea" :rows="5" v-model="setting_configure.value" placeholder="sql"></el-input>
            <br>
            <p></p>
            <p>sql</p>
          </el-tab-pane>
          <el-tab-pane name="" label="">
            <el-input v-model="setting_configure.value" placeholder="" ></el-input>
            <br>
            <p></p>
            <p>1.a=5, a=8.55, a="", a=[1,2,3] </p>
            <p>2.a=randint(1,10)+2-3-12 #1-10.</p>
            <p>3.a=time.time()</p>
            <p>4.a=IDcard()</p>
            <p> 5.a=random_adress()</p>
            <p></p>
            <p></p>

          </el-tab-pane>
          <el-tab-pane name="mock" label="mock">
            <el-select v-model="setting_configure.select" style="width: 120px">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
            <el-input type="textarea" :rows="8" v-model="setting_configure.value" placeholder="" ></el-input>
            <br>
            <p></p>
            <p>1.</p>
            <p>2.urlheader</p>
          </el-tab-pane>
          <el-tab-pane name="" label="">
            <el-select v-model="setting_configure.select" style="width: 150px">
              <el-option label="request_header" value="request_header"></el-option>
              <el-option label="params" value="params"></el-option>
              <el-option label="request_body" value="request_body"></el-option>
            </el-select>
            <el-input  v-model="setting_configure.value" placeholder="" style="width: -webkit-calc(100% - 205px )" ></el-input>
            <br>
            <p></p>
            <p>a=55</p>
          </el-tab-pane>
          <el-tab-pane name="" label="">
             <el-select v-model="setting_configure.select" style="width: 150px" placeholder="">
              <el-option label="request_header" value="request_header"></el-option>
              <el-option label="params" value="params"></el-option>
            </el-select>
            <el-input  v-model="setting_configure.value" placeholder="" style="width: -webkit-calc(100% - 205px )" ></el-input>
            <br>
            <p></p>
            <p>sign = python ,(md5,shar1)</p>
          </el-tab-pane>
          <el-tab-pane name="" label="">
            <el-input type="textarea" :rows="10" placeholder=""></el-input>
            <p></p>
          </el-tab-pane>


        </el-tabs>
      </div>
      <div v-else="">...</div>
    </el-card>
    <h1> &#12288
      <el-button size="mini" type="primary" @click="add_apis"></el-button>
    </h1>
    <el-tree
        id="left_div"
        style="width:300px;overflow-y: auto"
        :data="apis"
        show-checkbox
        node-key="id"
        :check-strictly="true"
        :default-checked-keys="dck"
        :default-expanded-keys="dek"
        @check-change="handleCheckChange">
      <span slot-scope="{data}">
<!--        <span size="mini">{{ data.label }} &#12288</span>-->
        <span size="mini" v-text="get_label(data.label)"></span>&nbsp;
        <el-button
            type="text"
            size="mini"
            @click="() => set(data)"
            style="color: darkturquoise">
              
        </el-button>
        <el-button
            v-if="v_if(data)"
            type="text"
            size="mini"
            @click="() => add_configure(data)"
            style="color: darkgreen">
              
        </el-button>
        <el-button
            type="text"
            size="mini"
            @click="() => remove(data)">
              
        </el-button>

      </span>
    </el-tree>


    <el-card style="position: fixed;bottom: 15px;width:-webkit-calc(100% - 240px);min-height:100px;">
      <el-input v-model="userable_par" type="textarea" :row="5" style="float:right;width: -webkit-calc(100% - 300px)"
                placeholder=""></el-input>
      <el-button size="mini" type="success"></el-button> &nbsp;<span></span> <span
        style="color: #42b983">{{ doing_api }}...</span>
      <br>
      <el-button style="margin-top: 10px" size="mini" type="primary"></el-button> &nbsp;<span></span>
      <strong><span style="color: darkgreen"></span><br></strong>


    </el-card>


  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Project_case",
  data() {
    return {
      choose_tab_pane:'',
      right_api: false,
      right_configure: false,
      doing_api: '',
      userable_par: '',
      setting_api: {},
      setting_configure: {},
      dck: [], // 
      dek: [], //
      ref: "tree",
      tableData: [{name: "name", des: 'des'}],
      activeName: "", //bodytab
      apis: [
        // {id:1,type:'api',label:'1',children:[{id:'1_1',type:'configure',label:'1'},{id:'1_2',type:'configure',label:'2'},{id:'1_3',type:'configure',label:'3'}]},
      ],
      response_data:{
        R:'',  // Response
        RD:'', // ResponseData
        CR:'', // ConfigureResult
      },
    }
  },
  props: ["project_id",],
  mounted() {
    document.getElementById('right_id').style.minHeight = (document.documentElement.clientHeight - 240).toString() + 'px';
    document.getElementById('right_id').style.maxHeight = (document.documentElement.clientHeight - 240).toString() + 'px';
    document.getElementById('left_div').style.height = (document.documentElement.clientHeight - 286).toString() + 'px'
    axios.get('http://localhost:8000/get_apis/', {
      params: {
        project_id: this.project_id,
      }
    }).then(res => {
      this.apis = res.data
    });
    // 
    axios.get('http://localhost:8000/get_dck/', {
      params: {
        project_id: this.project_id,
      }
    }).then(res => {
      this.dck = res.data
    });

  },
  methods: {
    up_api(){
      axios.get('http://localhost:8000/up_api/',{
        params:{
          api_id:this.setting_api.id,
          project_id:this.project_id,
        }
      }).then(res=>{
        this.apis=res.data;
        this.right_api=false;
        this.dek=[this.setting_api.id]
      })
    },
    down_api(){
      axios.get('http://localhost:8000/down_api/',{
        params:{
          api_id:this.setting_api.id,
          project_id:this.project_id,
        }
      }).then(res=>{
        this.apis=res.data;
        this.right_api=false;
      })
    },
    up_configure(){
      axios.get('http://localhost:8000/up_configure/',{
        params:{
          configure_id:this.setting_configure.id,
          project_id:this.project_id,
        }
      }).then(res=>{
        this.apis=res.data;
        this.right_configure=false;
        this.dek=[parseInt(this.setting_configure.id.split('_')[0])] // 
      })
    },
    down_configure(){
      axios.get('http://localhost:8000/down_configure/',{
        params:{
          configure_id:this.setting_configure.id,
          project_id:this.project_id,
        }
      }).then(res=>{
        this.apis=res.data;
        this.right_configure=false;
        this.dek=[parseInt(this.setting_configure.id.split('_')[0])] // 
      })
    },
    // get_label(label){
    //   if (label.length>12){
    //     var new_label = label.substring(0,12) + '...'
    //   }
    //   else{
    //     var new_label = label
    //   }
    //   return new_label
    //
    // },
    get_label(label){
      return label.substring(0,12) + ( (label.length>12)?'...':'')
    },
    handleCheckChange(data, checked) {
      console.log(data, checked);
      axios.get('http://localhost:8000/set_dck', {
        params: {
          project: this.project_id,
          dck: this.$refs.tree.getCheckedKeys().toString(),
        }
      }).then(res => {
        this.dck = res.data
      })
    },
    // 
    set(data) {
      console.log(data)
      if (data.type == 'api') {
        this.right_api = true;
        this.right_configure = false;
        this.setting_api = data;
      } else {
        this.right_api = false;
        this.right_configure = true;
        this.setting_configure = data;
      }

    },
    // 
    remove(data) {
      if (confirm("") == false) {
        return
      }
      axios.get('http://localhost:8000/remove_ac', {
        params: {
          project_id: this.project_id,
          id: data.id,
        }
      }).then(res => {
        this.apis = res.data
      })
    },


    v_if(data) {
      if (data.type == 'api') {
        return true
      } else {
        return false
      }
    },
    handleClick(tab, event) {
      console.log(tab, event)
    },
    // 
    add_configure(data) {
      axios.get('http://localhost:8000/add_configure/', {
        params: {
          project_id: this.project_id,
          id: data.id,
        }
      }).then(res => {
        this.apis = res.data;
        this.dek = [data.id];
      })

    },
    add_apis() {
      axios.get('http://localhost:8000/add_apis/', {
        params: {
          project_id: this.project_id,
        }
      }).then(res => {
        this.apis = res.data
        this.dek = []
      })

    },
    save_configure(){
      axios.post('http://localhost:8000/save_configure/',this.setting_configure).then(res=>{
        this.$message({
          message:'',
          type:'success'
        })

      })

    },
    save_api(){
      axios.post('http://localhost:8000/save_api/',this.setting_api,{
        params:{
          project_id:this.project_id
        }}).then(res=>{
          this.apis=res.data;
          this.$message({
            message:'',
            type:'success'
          })
      })
    },
    send_api(){
      axios.post('http://localhost:8000/send_api/',this.setting_api,{
        params:{
          project_id:this.project_id,
        }
      }).then(res=>{
        this.response_data=res.data;
        this.choose_tab_pane='Response';
      })

    },
    run(){

    },
    report(){

    },

    // tab
    choose_configure_method(tab,event){
      console.log(tab,event)
      this.setting_configure.select=''
      this.setting_configure.value=''
      this.setting_configure.method=tab.label;
    },
    // 
    add_params(){
      this.setting_api.params.push( {} )
    },
    // 
    del_params(index){
      this.setting_api.params.splice(index,1)
    },
    // 
    add_headers(){
      this.setting_api.headers.push( {} )
    },
    // 
    del_headers(index){
      this.setting_api.headers.splice(index,1)
    },
    // 
    add_payload_fd(){
      this.setting_api.payload_fd.push( {} )
    },
    // 
    del_payload_fd(index){
      this.setting_api.payload_fd.splice(index,1)
    },
    // 
    add_payload_xwfu(){
      this.setting_api.payload_xwfu.push( {} )
    },
    // 
    del_payload_xwfu(index){
      this.setting_api.payload_xwfu.splice(index,1)
    },


  }
}
</script>

<style scoped>
.smallfont {
  font-size: xx-small;
  color: gray;
}
P{
  font-size: xx-small;
  color: gray;
}
</style>